.catalog{
	&__heading{
		margin: 0 0 20px;
		height: 30px;
		line-height: 26px;
	}
	&__header{
		font-size: 14px;
		line-height: 30px;
		margin-bottom: 20px;
		text-align: right;
		.btn{
			margin-left: 16px;
			& + &{
				margin-left: 24px;
			}
		}
	}
	&__header-title{
		font-size: 14px;
		line-height: 29px;
		vertical-align: top;
	}
	&__footer{
		margin-top: 18px;
		margin-bottom: 91px;
	}
	&__total{
		margin-right: 110px;
	}
}
.catalog__list{
	margin: 0;
	padding: 0;
	list-style: none;
	overflow: hidden;
}
.catalog__item{
	float: left;
	width: (100/3)*1%;
	margin-bottom: 20px;
	&:nth-child(3n){
		padding-left: 10px;
	}
	&:nth-child(3n-1){
		padding: 0 10px;
	}
	&:nth-child(3n-2){
		padding-right: 10px;
	}
}