.site-header {
	background: -webkit-linear-gradient(rgba(43,25,67,0.35), rgba(43,25,67,0.35)), url('../img/background.jpg') no-repeat;
	background: -o-linear-gradient(rgba(43,25,67,0.35), rgba(43,25,67,0.35)), url('../img/background.jpg') no-repeat;
	background: linear-gradient(rgba(43,25,67,0.35), rgba(43,25,67,0.35)), url('../img/background.jpg') no-repeat;
	background-size: cover;
	z-index: 10;
	position: relative;
	.form {
		margin-top: 48px;
	}
	&__container {
		.clearfix();
	}
	&__logo {
		float: left;
		overflow: hidden;
	}
	&__logo-img {
		width: 276px;
		height: 135px;
		margin-top: 5px;
		margin-left: -26px;
	}
	&__logo-text {
		.sans();
		font-size: 36px;
		color: #fff;
	}
	&__info {
		margin-top: 63px;
		text-align: center;
		width: 286px;
		position: absolute;
		top: 0px;
		left: 50%;
		margin-left: -143px;
	}
	&__info-list {
		display: block;
		list-style: none;
		padding: 0;
		margin: 0;
		width: 100%;
		position: relative;
		.clearfix();
	}
	&__info-item {
		float: left;
		font-size: 14px;
		float: left;
		&:last-child {
			float: right;
		}
	}
	&__info-link {
		color: #fff;
		transition: opacity .3s;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=30)";
		&:hover {
			color: #fff;
			opacity: .8;
			-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
			text-decoration: none;
		}
		&_strong {
			font-weight: bold;
			font-size: 16px;
		}
	}
}
.ie-8 .site-header {
	-ms-filter:"progid:DXImageTransform.Microsoft.gradient( startColorstr='#592b1943', endColorstr='#592b1943',GradientType=0 ) progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../img/background.jpg', sizingMethod='scale')";
    filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#592b1943', endColorstr='#592b1943',GradientType=0 ) progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../img/background.jpg', sizingMethod='scale');
	position: relative;
	.container{
		position: relative;
		z-index: 1;
	}
}
.ie-8 .articles .site-header{
	-ms-filter:"progid:DXImageTransform.Microsoft.gradient( startColorstr='#592b1943', endColorstr='#592b1943',GradientType=0 ) progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../img/backarticle.jpg', sizingMethod='scale')";
    filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#592b1943', endColorstr='#592b1943',GradientType=0 ) progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../img/backarticle.jpg', sizingMethod='scale');
}
.catalog,
.product-page,
.articles{
	.site-header {
		background: -webkit-linear-gradient(rgba(43,25,67,0.35), rgba(43,25,67,0.35)), url('../img/backarticle.jpg') no-repeat;
		background: -o-linear-gradient(rgba(43,25,67,0.35), rgba(43,25,67,0.35)), url('../img/backarticle.jpg') no-repeat;
		background: linear-gradient(rgba(43,25,67,0.35), rgba(43,25,67,0.35)), url('../img/backarticle.jpg') no-repeat;
		background-size: cover;
		z-index: 10;
		position: relative;
	}
	.main-menu {
		margin-top: 44px;
		padding-bottom: 120px;
		width: 100%;
		&__title{
			display: none;
		}
	}
}
.information-title{
	position: absolute;
	bottom: 34px;
	color: #fff;
	margin: 0;
	font-weight: 300;
	font-size: 14px;
}